.App {
  touch-action: none;
  background-color: #f5f5f5;
  display: block;
  height: 100vh;
  overflow-y: hidden;
  .App-content {
    text-align: center;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    display: flex;
    font-size: calc(10px + 2vmin);
  }
}

input,
textarea,
button,
select,
a {
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}

.nav-panel{
  .ms-Overlay{
    background-color: transparent;
  }
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}


.App-link {
  color: #61dafb;
}
