.logging-mode-container{
  color: white;

  .timer{
    margin-top: -220px;
    margin-bottom: 80px;
  }

  .timer-controls {
    position: fixed;
    top: 30%;
  }

  .events-container{
    position: fixed;
    top: 50%;
    width: 100%;
    max-width: 500px;
    margin-left: auto;
    margin-right: auto;
    height: 60%;
  }

  .button {
    height: 80px;
    width: 80px;
    border-radius: 40px;
  }

  .button.is-disabled {
    color: gray;
    background: lightgray;
  }

  .icon {
    font-size: 40px;
  }
}
